import React from 'react'
import { useTheme, Typography } from '@mui/material'
import Button from './Button'
import { Dots } from '../../../pages/Pool/styleds'

export default function ActionButton({
  error,
  pending,
  success,
  onAction,
  actionText,
  pendingText,
  height,
  width,
  disableAction,
  successText
}: {
  error?: string | undefined
  pending?: boolean
  success?: boolean
  onAction: (() => void) | undefined
  actionText: string
  pendingText?: string
  successText?: string
  height?: string
  width?: string
  disableAction?: boolean
}) {
  const theme = useTheme()

  return (
    <>
      {error || pending ? (
        <Button disabled>
          {pending ? (
            <>
              <Dots>{pendingText || 'Waiting Confirmation'}</Dots>
            </>
          ) : (
            error
          )}
        </Button>
      ) : success ? (
        <Button disabled>
          <Typography variant="inherit">{successText ?? actionText}</Typography>
        </Button>
      ) : (
        <Button
          onClick={onAction}
          disabled={disableAction}
        >
          {actionText}
        </Button>
      )}
    </>
  )
}
